.list{
    max-width: 90%;
    border-radius: 5px;
    background: #ce0955b9;
    
    padding: 10px;
    margin: 5px auto;
    position: relative;
}
.listInput{
    width:90%;
    background: transparent;
    color: #fff;
    font-weight: 500;
    font-size: 14px;
    padding: 5px;
    border: 0;
}

.listInput:focus{
    outline: none;
}
.faicon{
    position: absolute;
    right: 10px;
    color: #fff;
    cursor: pointer;
}