.App{
  width: 400px;
  min-height: 500px;
  margin: 40px auto;
  padding-bottom: 10px;
  background: rgb(37, 167, 228);
}
.header-text{
  max-width: 100%;
  text-align: center;
  font-size: 20px;
  padding: 10px;
  background: #ce0955b9;
  color: #fff;
  font-family: monospace;
}
#todo-form input[type="text"]{
  width: 250px;
  padding: 10px;
  margin: 10px;
  border: 0;
  border-radius: 5px;
  background: rgb(34, 25, 22);
  color: #fff;
}

#todo-form input[type="submit"]{
  width: 80px;
  padding: 10px;
  margin: 10px;
  border: 0;
  border-radius: 5px;
  background: rgb(128, 151, 9);
  color: #fff;
}

#todo-form input[type="text"]::placeholder{
  color: #fff;
}

#todo-form input[type="text"]:focus{
  outline: none;
}